.page{
    background-size: 100%;
    height: 100%;
    display: "flex"; 
    flex: 1 1;
    flexWrap: "wrap";
    justifyContent: "center";
    text-align: center;
    margin-top: 0px;
    padding-top: 0px;
    margin-bottom: 30px;
}

.title{
    color: black;
    font-size: 85px;
    font-family: "Dancing Script", cursive;
    position: relative;
    margin:0;
    line-height: 160px; 
    font-weight: normal; 
    margin-bottom: 0px; 
    margin-top: 40px; 
    text-align: center; 
    text-shadow: 0 1px 1px #fff;
}

.makeButton{
    margin-top: 10px;
    border-color: mistyrose;
    font-family: 'Rokkitt', serif;
    color: black;
    font-size: 25px;
    border-style: double;
    background: transparent;
    padding: 6px;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 10px;
}

.makeButton:hover {
    background: rgba(202, 202, 202, 0.5);
}

.makeButton:active {
    background: rgba(94, 94, 94, 0.5);
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.label{
    font-family: 'Rokkitt', serif;
}

.form{
    display: "flex";
    width: 200;
}

